@charset "UTF-8";
@import "variables";
@import "../../../sass/func";
strong {
    font-weight: bold;
}

.content-aboutus {
    padding: pxToRem(80px) 0;
    font-size: pxToRem(26px);
    color: #333;
    .logo {
        @include background("../images/aboutus/logo.png", 180px, 190px);
        margin: 0 auto;
        margin-bottom: pxToRem(32px);
    }
    section {
        padding-top: pxToRem(50px);
        margin: 0 pxToRem(32px) 0;
        h4 {
            padding-bottom: pxToRem(34px);
            font-size: pxToRem(32px);
            font-weight: bold;
        }
        h5 {
            padding-bottom: pxToRem(12px);
            font-size: pxToRem(28px);
            color: #333333;
            font-weight: 700;
        }
        strong{
          padding-top:pxToRem(20px);
          color:#333;
          font-weight: 700;
          display:block;
          padding-bottom:pxToRem(12px);
        }
        p {
            margin-bottom: pxToRem(20px);
            line-height: pxToRem(52px);
            // font-size: pxToRem(28px);
            text-indent: 2em;
            text-align: justify;
            color: #666;
        }
        div {
            margin-bottom: pxToRem(30px);
            line-height: pxToRem(36px);
        }
    }
    .section_0 {
        position: relative;
        &:after {
            @include border(bottom, #f1f1f1);
        }
    }
    .sec-team {
        padding-bottom: pxToRem(40px);
    }
    .section_2 {
        padding-bottom: pxToRem(20px);
    }
    .inline-block {
        display: inline-block;
        i {
            vertical-align: middle;
        }
    }
    .float-right {
        float: right;
    }
    .sec-team {
        padding-left: 0;
        padding-right: 0;
        h4 {
            padding-left: pxToRem(32px);
        } // overflow: hidden;
    } // swiper
    @at-root .swiper-panel {
        $slideWidth: 294px * 2;
        $space: 41px;
        overflow: hidden;
        position: relative;
        margin-bottom: 0 !important;
        height: pxToRem(590px + 12px + 22px + 60px);
        .swiper-container {
            overflow: visible !important;
            position: absolute;
            top: 0;
            bottom: 0;
            left: pxToRem(- $slideWidth + $space);
            width: pxToRem($slideWidth * 3 + $space * 3);
        }
        .swiper-slide {
            padding-right: pxToRem($space);
            width: pxToRem($slideWidth + $space);
            box-sizing: border-box;
        }
        .slide-content {
            box-sizing: border-box;
            border-radius: 5px;
            padding: 0 pxToRem(40px) pxToRem(12px); // margin-right: pxToRem(40px);
            width: pxToRem($slideWidth); // height: pxToRem(220px);
            text-align: justify;
            box-shadow: 0 pxToRem(12px) pxToRem(22px) 0 rgba(184, 194, 184, .3);
            figure {
                text-align: center;
                p {
                    text-indent: unset;
                    color: #666;
                }
            }
            img {
                width: pxToRem(162px);
                height: pxToRem(162px);
            }
            strong {
                padding: pxToRem(12px);
            }
            p {
                font-size: pxToRem(24px);
                line-height: pxToRem(44px);
                color: #999;
            }
            .desc {
                height: pxToRem(400px);
            }
        }
    }
}

// icons
.icon-wx {
    // @include inline-background("../images/aboutus/wx.png", 72px, 72px);
    @include inline-background("data:img/jpg;base64,", 72px, 72px);
}

.icon-wb {
